.btnYo{
    position: relative;
    padding: 8px 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    border: none;
    text-transform: uppercase;
    margin: 0 0 15px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1;
    letter-spacing: 1px;
    background-color: transparent;
    outline: none;
    color: #252527;
    cursor: pointer;
    z-index: 0;
    max-width: 180px;
}

.btnYo::after{
    content: "";
    position: absolute;
    width: 50%;
    height: 8px;
    background: #ea548d;
    bottom: 0;
    z-index: -1;
    transition: all 0.2s ease-in-out;
}

.btnYo:hover::after{
    width: 100%;
    height: 100%;
}
