.projectsBlock{
    background-color: #1f1f20;
    min-height: 50vh;
    padding: 100px 0 150px 0;
}

.container{
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
}

.tittle{
    background: gainsboro;
}

.projects{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 1252px){
    .projects{
        justify-content: center;
    }
}