.contactsBlock{
    background-color: #252527;
    min-height: 30vh;
    padding: 100px 0 150px 0;
}

.container{
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.tittle{
    width: 120px;
    background: gainsboro;
}

.form{
    position: relative;
    opacity: 0.7;
    display: flex;
    flex-direction: column;
    width: 500px;
    height: 310px;
    justify-content: space-between;
}

.formArea{
    padding: 15px;
    color: #fffefd;
}

.messageArea{
    min-height: 160px;
    padding: 15px;
    color: #fffefd;
}

.formArea, .messageArea{
    box-sizing: border-box;
    background: #252527 !important;
    border: 1px solid #4a4a4a;
    font-size: 12px;
    outline: none;

}

.formArea:focus, .messageArea:focus{
    border: 1px solid transparent;
    transition: all 0.4s ease-in-out 0s;
}

@media screen and (max-width: 626px) {
    .form{
        width: 100%;
    }
}

