.main{
    height: 100vh;
    /*background-color: #1f1f20;*/
    display: flex;
    justify-content: center;
    padding: 100px 0;
}


.greeting{
    position: relative;
    display: flex;
    flex-direction: column;
}

.greeting span:nth-child(1){
    font-size: 16px;
}

.greeting span:nth-child(2),
.greeting span:nth-child(2) span{
    font-size: 44px;
    font-weight: 700;
    font-stretch: normal;
    line-height: 1.2;
    letter-spacing: 5px;
    font-family: 'Montserrat', sans-serif;
    position: relative;
    z-index: 0;
    display: inline-block;
}

.greeting span:nth-child(2) span:before{
    content: "";
    position: absolute;
    width: 100%;
    height: 20px;
    background-color: #ea548d;
    bottom: 0px;
    z-index: -1;
}

.h1 {
    font-size: 27px;
}

.photo{
    width: 300px;
    height: 400px;
    background-color: gainsboro;
    position: relative;
    z-index: 1;
}

.image{
    width: 300px;
    height: 400px;
    background-color: gainsboro;
    z-index: 1;
}

.photo .image:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 105%;
    border: 5px solid #ea548d;
    left: 7px;
    top: -15px;
    z-index: -1;
}


@media screen and (max-width: 377px) {
    .greeting{
        width: 100%;
    }
    .photo{
        width: 100%;
        height: 50%;
    }
    .image{
        width: 100%;
        height: 100%;
    }
}

.container{
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 980px) {
    .container{
        flex-direction: column;
        justify-content: center;
    }
}
