.footerBlock{
    background-color: #1f1f20;
    padding: 45px 0 0;
}

.container{
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icons{
    width: 500px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.icon{
    position: relative;
    width: 50px;
    height: 50px;
}

.icon a img{
    height: 100%;
}

.rec{
    position: relative;
    width: 200px;
}

@media screen and (max-width: 626px) {
    .icons{
        width: 100%;
    }
}

@media screen and (max-width: 341px) {
    .icon{
        width: 40px;
        height: 40px;
    }
}