.header{
    height: 80px;
    width: 100%;
    background-color: #262626;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 2;
    border-bottom: 1px solid #ea548d;
}

.container{
    display: flex;
    justify-content: flex-end;
    width: 80%;
}

@media screen and (max-width: 900px){
    .container{
        display: flex;
        justify-content: center;
        width: 100%;
    }
}
