.skillsBlock{
    background-color: #252527;
    display: flex;
    flex-wrap: wrap;
    padding: 100px 0;
}

.skillsContainer{
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
}

.tittle{
}

.line{
    height: 1px;
    width: 55px;
    margin: -15px 0 30px 0;
    background-color: #ea548d;
}

.headerTitle{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    line-height: 1.2;
    letter-spacing: 5px;
    font-weight: 300;
    font-size: 24px;
}


.skills{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 850px){
    .skills{
        flex-direction: column;
        align-items: center;
    }
}
