.skill{
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 50px;
}

.icon{
    height: 60px;
    width: 60px;
    transform: rotate(45deg);
    background-color: rgba(255,255,255,0.1);
    display: flex;
    justify-content: center;
    align-items: center;
}

.skillTitle{
    position: relative;
    text-transform: uppercase;
    margin: 20px 0 15px;
    padding: 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.2;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 1px;
}

.icon img{
    transform: rotate(-45deg);
    height: 70%
}

.description{
    position: relative;
}

@media screen and (max-width: 600px){
    .skill{
        width: 100%;
    }
}
