.project{
    position: relative;
    width: 500px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    background-color: rgba(255,255,255,0.1);
    margin: 0 0 80px;
}

.bl1{
    width: 100%;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    opacity: 0.7;
}

.bl1:after{
    content: '';
    height: 100%;
    width: 100%;
    opacity: 0.5;
}

.btn{
    background-color: #555758;
}

.bl2{
    width: 100%;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
}


@media screen and (max-width: 530px){
    .bl1.style{
        background-size: 110%;
    }
}

.title{
    padding: 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.2;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 1px;
    margin: 0 15px;
}

.text{
    text-align: start;
    font-size: 12px;
    font-family: Roboto,sans-serif;
    margin: 0 15px;
}

@media screen and (max-width: 620px){
    .project{
        width: 100%;
        justify-content: center;
    }
}